<template><section><h1>CoplanarPolygonGeometry, CoplanarPolygonOutlineGeometry</h1>
<ul>
<li>The <code v-pre>vc-geometry-polygon-coplanar</code> component is used to load a geometry that describes a coplanar polygon from an array of positions.</li>
<li>The <code v-pre>vc-geometry-outline-polygon-coplanar</code> component is used to load a geometry that describes coplanar polygon outline from an array of positions.</li>
<li>It needs to be used as a sub-component of <code v-pre>vc-instance-geometry</code>, can be mounted on <code v-pre>vc-primitive</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load CoplanarPolygonGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-polygon-coplanar ref="polygonCoplanar" :positions="positions" :vertexFormat="vertexFormat"></vc-geometry-polygon-coplanar>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributesOutline">
            <vc-geometry-outline-polygon-coplanar ref="polygonCoplanarOutline" :positions="positions"></vc-geometry-outline-polygon-coplanar>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
