<h2>Instance Properties</h2>
<h3><code v-pre>vc-geometry-polygon-coplanar</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>required</code> An array of positions that defined the corner points of the polygon.</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The vertex attributes to be computed.</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The rotation of the texture coordinates, in radians. A positive rotation is counter-clockwise.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid to be used as a reference.</td>
</tr>
</tbody>
</table>
