<h3><code v-pre>vc-geometry--outline-polygon-coplanar</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>required</code> An array of positions that defined the corner points of the polygon.</td>
